import React from 'react';

class AddItemForm extends React.PureComponent {
  render() {
    let itemTypeIcon;

    switch (this.props.itemTypeId) {
      default:
      case 0:
        itemTypeIcon = 'film';
        break;
      case 1:
        itemTypeIcon = 'book-open';
        break;
      case 2:
        itemTypeIcon = 'book';
        break;
    }

    return (
      <form id="addItemForm" onSubmit={this.props.addItem} className="flex mb5">
        <div className="mr5 width-4/5">
          <div className="flex">
            <input type="text" name="title" placeholder="title" value={this.props.title} onChange={this.props.handleItemChange} className="mr5 mb5 border-gray p5 width-full"/>
            <div id="cycleItemTypeButton" onClick={this.props.cycleItemType} className="flex justify-center items-center border-gray p5 text-center pointer">
              <i aria-hidden={true} className={'fas fa-' + itemTypeIcon + ' pointer'}></i>
            </div>
          </div>
          <div className="flex">
            <div id="date" className="flex mr5">
              <div className="mr5 width-1/3">
                <input type="text" name="month" maxLength="2" placeholder="month" value={this.props.month} onChange={this.props.handleItemChange} className="border-gray p5 width-full"/>
              </div>
              <div className="mr5 width-1/3">
                <input type="text" name="day" maxLength="2" placeholder="day" value={this.props.day} onChange={this.props.handleItemChange} className="border-gray p5 width-full"/>
              </div>
              <div className="width-1/3">
                <input type="text" name="year" maxLength="2" placeholder="year" value={this.props.year} onChange={this.props.handleItemChange} className="border-gray p5 width-full"/>
              </div>
            </div>
            <div id="toggleItemFirstButton" onClick={this.props.toggleItemIsFirst} className="flex justify-center items-center border-gray p5 lineHieght1 pointer">{(this.props.isFirst) ? 'X' : ''}</div>
          </div>
        </div>
        <button type="submit" id="addItemButton" className="border-gray p5 width-1/5 pointer">add</button>
      </form>
    );
  }
}

export default AddItemForm;
