import React from 'react';

import { getIconColor } from '../utils.js';

class Filters extends React.PureComponent {
  render() {
    const sortButton = this.props.isAsc ? 'fa-caret-square-up' : 'fa-caret-square-down';

    return (
      <div className="flex flex-col desktop-flex-row justify-between items-center mb5">
        <div className="flex items-center mr5 mb5 desktop-mb0 width-full desktop-flex1">
          <i aria-hidden={true} className={`mr5 fas fa-book text-xl desktop-text-l ${getIconColor(this.props.showBooks)} pointer`} onClick={this.props.toggleShowBooks} />
          <i aria-hidden={true} className={`mr5 fas fa-book-open text-xl desktop-text-l ${getIconColor(this.props.showComics)} pointer`} onClick={this.props.toggleShowComics} />
          <i aria-hidden={true} className={`mr5 fas fa-film text-xl desktop-text-l ${getIconColor(this.props.showMovies)} pointer`} onClick={this.props.toggleShowMovies} />
          <i aria-hidden={true} className={`mr5 fas fa-calendar text-xl desktop-text-l ${getIconColor(this.props.showDate)} pointer`} onClick={this.props.toggleShowDate} />
          <i aria-hidden={true} className={`mr5 fas fa-clone text-xl desktop-text-l ${getIconColor(this.props.showDuplicates)} pointer`} onClick={this.props.toggleShowDuplicates} />
          <i aria-hidden={true} className={`mr5 fas ${sortButton} text-xl desktop-text-l pointer`} onClick={this.props.toggleIsAsc} />
        </div>
        <div className="flex width-full desktop-flex-grow-1">
          <div className="mr5 width-1/3 desktop-width-100">
            <p className="border-black p5 width-full pointer" id="dateFrom" name="dateFrom" onClick={() => {this.props.setKeypad(true, 'dateFrom')}}>
              {this.props.dateFrom.label || (<span className="text-gray">from</span>)}
            </p>
          </div>
          <div className="mr5 width-1/3 desktop-width-100">
            <p className="border-black p5 width-full pointer" id="dateTo" name="dateTo" onClick={() => {this.props.setKeypad(true, 'dateTo')}}>
              {this.props.dateTo.label || (<span className="text-gray">to</span>)}
            </p>
          </div>
          <div className="width-1/3 desktop-width-100">
            <input className="border-black p5 width-full" name="title" onChange={this.props.filterTitle} placeholder="title" value={this.props.title} />
          </div>
        </div>
      </div>
    );
  }
}

export default Filters;
