import React from 'react';

class SignInForm extends React.PureComponent {
  render() {
    return (
      <form className="flex mb5" id="signInForm" onSubmit={this.props.signIn}>
        <div className="flex mr5 width-4/5">
          <div className="width-1/2 mr5">
            <input className="border-gray p5 width-full" name="username" onChange={this.props.handleLoginChange} placeholder="username" type="text" value={this.props.username} />
          </div>
          <div className="width-1/2">
            <input className="border-gray p5 width-full" name="password" onChange={this.props.handleLoginChange} placeholder="password" type="password" value={this.props.password} />
          </div>
        </div>
        <button className="border-gray p5 width-1/5 pointer" type="submit">
          sign in
        </button>
      </form>
    );
  }
}

export default SignInForm;
