import React from 'react';

class Items extends React.PureComponent {
  render() {
    let items = this.props.itemsFiltered.slice();

    const digitsTotal   = items.length.toString().length;

    if (!this.props.isAsc) {
      items.reverse();
    }

    items = items.map((item, index) => {
      const date  = (this.props.showDate) ? ` (${item.date.label})` : '';
      const id    = this.props.isAsc ? index + 1 : items.length - index;
      const title = item.isFirst ? item.title : `[${item.title}]`;

      const digitsCurrent = id.toString().length;
      const spacer        = ('').padStart(digitsTotal - digitsCurrent, ' ');

      let icon;

      switch (item.type) {
        default:
        case 'book':
          icon = (<i aria-hidden={true} className="mr5 fas fa-book"/>);
          break;
        case 'comic':
          icon = (<i aria-hidden={true} className="mr5 fas fa-book-open"/>);
          break;
        case 'movie':
          icon = (<i aria-hidden={true} className="mr5 fas fa-film"/>);
          break;
      }

      return (
        <div className="whitespace-pre" key={index}>
          {spacer}{id}. {icon}{title}{date}
        </div>
      );
    });

    items = (items.length > 0) ? items : (<div>...</div>);

    return (
      <section id="items" className="border-black p5">
        {items}
      </section>
    );
  }
}

export default Items;
